.Statistics {
    width: 500px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    color: #a0a1a1;
    background-color: #a0a1a1;
    padding: 50px;
  }
  .Title {
    display: block;
    margin: 0;
    padding: 0;
    text-align: center;
    line-height: 5;
    
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: lightslategrey;
    background-color: #fff;
  }
  .StatList {
    padding: 0;
    margin: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .Item {
    width: 100%;
    list-style: none;
    text-align: center;
    color: rgb(39, 39, 11);
    padding-top: 10px;
  }
 